.main-hero-div {
  background: var(--new, linear-gradient(130deg, #492FB2 -1.74%, #ADADAD 108.02%));
  padding: 125px 0px;
  position: relative;
  z-index: 2;

  &::before,
  &::after {
    content: '';
    position: absolute;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    /* Adjust size as needed */
    height: 100%;
    z-index: 0;
  }

  &::before {
    background-image: url('../../../../assets/images/left-rings.png');
    top: 0%;
    background-position: left top;
    z-index: 0;
  }

  &::after {
    background-image: url('../../../../assets/images/right-rings.png');
    background-position: right top;
    bottom: 10%;
    z-index: 0;
  }
}

.left-heading-div {
  h1 {
    color: rgba(255, 255, 255, 0.59);
    font-family: Urbanist;
    font-size: 56px;
    font-style: normal;
    font-weight: 500;
    line-height: normal;
    margin: 0px;

    span {
      color: #FFF;
      font-family: Urbanist;
      font-size: 56px;
      font-style: normal;
      font-weight: 500;
      line-height: normal;
    }
  }

  p {
    color: #FFF;
    font-family: Urbanist;
    font-size: 22px;
    font-style: normal;
    font-weight: 400;
    line-height: 35px;
    letter-spacing: -0.1px;
    margin: 35px 0px;
  }

  width: 60%;
  z-index: 2;
}

.right-logo-div {
  width: 40%;
  z-index: 2;
}

.aboutus-main-div {
  background: #150f22;
  padding: 90px 0;

  .left-images-div {
    position: relative;
    min-height: 400px;

    img {
      position: absolute;
      border-radius: 50%;
      object-fit: cover;
      border: 4px solid #150f22;
      box-shadow: 0 0 15px rgba(0, 0, 0, 0.3);

      // ✅ grayscale and transition
      filter: grayscale(100%);
      transition: filter 0.3s ease, transform 0.3s ease;

      &:hover {
        filter: grayscale(0%);
        transform: scale(1.1);
        z-index: 5; // bring hovered image to front
      }
    }

    .image1 {
      width: 280px;
      height: 280px;
      top: 0;
      left: 0;
      z-index: 3;
      transform-origin: right;
      transition: filter 0.3s ease, transform 0.3s ease;

    }

    .image2 {
      width: 180px;
      height: 180px;
      top: 30px;
      right: 25%;
      z-index: 2;
      transform-origin: left;
      transition: filter 0.3s ease, transform 0.3s ease;

    }

    .image3 {
      width: 180px;
      height: 180px;
      bottom: 0;
      left: 35%;
      z-index: 1;
      transform-origin: top;
      transition: filter 0.3s ease, transform 0.3s ease;

    }
  }

  .right-content-div {
    h5 {
      color: #fff;
      font-family: Urbanist;
      font-size: 12px;
      font-weight: 400;
    }

    h2 {
      color: #fff;
      font-family: Urbanist;
      font-size: 40px;
      font-weight: 500;
    }

    p {
      color: #8d8d8d;
      text-align: justify;
      font-family: Urbanist;
      font-size: 22px;
      font-weight: 400;
      line-height: 35px;
      letter-spacing: -0.1px;
    }
  }
}

.btn-read {
  border-radius: 4px;
  border: 1px solid #FFF;
  opacity: 0.71;
  display: inline-flex;
  padding: 12px 24px;
  justify-content: center;
  align-items: center;
  color: #FFF;
  text-align: center;
  font-family: Urbanist;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  /* 150% */
  letter-spacing: 0.15px;
  gap: 8px;
  margin-top: 32px;
}

.btn-read:hover {
  border-radius: 4px;
  border: 1px solid #FFF;
  opacity: 0.71;
  display: inline-flex;
  padding: 12px 24px;
  justify-content: center;
  align-items: center;
  color: #150f22;
  background-color: #fff;
  text-align: center;
  font-family: Urbanist;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
  /* 150% */
  letter-spacing: 0.15px;
  gap: 8px;
}


/* Desktop Large (>= 1200px) – already looks good, no change needed */

/* Tablet (>= 768px and < 1200px) */
@media (max-width: 1400px) {

  .left-heading-div h1,
  .left-heading-div h1 span {
    font-size: 46px !important;
  }

  .left-images-div {

    width: 100% !important;

    .image1 {
      width: 280px;
      height: 280px;
      top: 0;
      left: 0;
      z-index: 3;
      transform-origin: right;
      transition: filter 0.3s ease, transform 0.3s ease;

    }

    .image2 {
      width: 180px;
      height: 180px;
      top: 30px;
      right: 15% !important;
      z-index: 2;
      transform-origin: left;
      transition: filter 0.3s ease, transform 0.3s ease;

    }

    .image3 {
      width: 180px;
      height: 180px;
      bottom: 0;
      left: 41% !important;
      z-index: 1;
      transform-origin: top;
      transition: filter 0.3s ease, transform 0.3s ease;

    }
  }

  .right-content-div {
    width: 100% !important;
  }

}

@media (max-width: 1199.98px) {
  .left-heading-div h1 {
    font-size: 44px;
  }

  .left-heading-div p {
    font-size: 18px;
    line-height: 30px;
  }

  .right-logo-div img {
    max-width: 100%;
  }

  .aboutus-main-div .left-images-div .image1 {
    width: 220px;
    height: 220px;
  }

  .aboutus-main-div .left-images-div .image2,
  .aboutus-main-div .left-images-div .image3 {
    width: 170px;
    height: 170px;
  }

  .aboutus-main-div .left-images-div .image3 {
    left: 29% !important;
  }

  .aboutus-main-div .right-content-div h2 {
    font-size: 32px;
  }

  .aboutus-main-div .right-content-div p {
    font-size: 18px;
    line-height: 30px;
  }
}

/* Mobile (>= 576px and < 768px) */
@media (max-width: 767.98px) {
  .main-hero-div .container {
    flex-direction: column;
    text-align: center;
    gap: 30px;

  }

  .left-heading-div {
    width: 100%;
    order: 2;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .left-heading-div h1,
  .left-heading-div h1 span {
    font-size: 36px;
  }

  .left-heading-div p {
    font-size: 16px;
    line-height: 28px;
  }

  .right-logo-div {
    width: 100%;
    order: 1 !important;
  }

  .right-logo-div img {
    width: 60%;
    max-width: 200px;
  }

  .aboutus-main-div .container {
    flex-direction: column;
    text-align: center;
  }

  .aboutus-main-div .left-images-div {
    width: 100%;
    min-height: 350px;
    margin-bottom: 40px;
  }

  .aboutus-main-div .left-images-div .image1 {
    width: 180px;
    height: 180px;
  }

  .aboutus-main-div .left-images-div .image2 {
    top: -10px !important;
    right: 0px !important;
  }

  .aboutus-main-div .left-images-div .image2,
  .aboutus-main-div .left-images-div .image3 {
    width: 150px !important;
    height: 150px !important;
  }

  .aboutus-main-div .right-content-div {
    width: 100%;
  }

  .aboutus-main-div .right-content-div h2 {
    font-size: 28px;
  }

  .aboutus-main-div .right-content-div p {
    font-size: 16px;
    line-height: 28px;
  }

  .btn-read {
    padding: 10px 20px;
    font-size: 14px;
  }
}

/* Extra Small Devices (< 576px) */
@media (max-width: 575.98px) {

  .left-heading-div h1,
  .left-heading-div h1 span {
    font-size: 28px;
  }

  .left-heading-div p {
    font-size: 14px;
    line-height: 24px;
  }

  .right-logo-div img {
    width: 100%;
    max-width: 180px;
  }

  .aboutus-main-div .left-images-div {
    min-height: 300px;
  }

  .aboutus-main-div .left-images-div .image1 {
    width: 150px;
    height: 150px;
  }

  .aboutus-main-div .left-images-div .image2,
  .aboutus-main-div .left-images-div .image3 {
    width: 100px;
    height: 100px;
  }

  .aboutus-main-div .right-content-div h2 {
    font-size: 24px;
  }

  .aboutus-main-div .right-content-div p {
    font-size: 14px;
    line-height: 24px;
  }

  .btn-read {
    padding: 8px 16px;
    font-size: 13px;
  }
}